import React, { useState, useEffect } from "react";
import Css from "./politics.module.css";
import PoliticsCard from "./politics_card/politicsCard";

export default function Politics() {
  const [data, setData] = useState([]);
  useEffect(() => {
    fetch("http://176.126.164.176:8000/api/politics/")
      .then((res) => res.json())
      .then((data) => {
        setData(data);
      });
  }, []);
  return (
    <div>
      <p className="news_title">Саясат</p>
      <div className={Css.container}>
        {data.map((item) => {
          return <PoliticsCard data={item} />;
        })}
      </div>
    </div>
  );
}
