import React, {useState, useEffect} from 'react'
import Css from "./education.module.css"
import EducationCard from './education_card/educationCard'
import Pagination from "../pagination/pagination";

export default function Education() {
    const [data, setData] = useState([])
    const [pages, setPages] = useState(0)
    const [active, setActive] = useState(1)
    const limit = 4;
    const getEducation = (page = 0) => {
        setActive(page)
        fetch(`http://176.126.164.176:8000/api/education/?limit=${limit}&offset=${page}`)
            .then((res) => res.json())
            .then((data) => {
                console.log(data)
                if(data.count < limit) {
                    setPages(1)
                }else {
                    setPages(Math.ceil(data.count / limit))
                }
                setData(data.results)
            })
    }
    useEffect(() => {
        getEducation()
    }, [])
    return (
        <div>
            <p className="news_title">Билим берүү</p>
            <div className={Css.container}>
                {
                    data.map((item) => {
                        return <EducationCard data={item} key={item.id}/>
                    })
                }
            </div>
            <br/>
            <br/>
            <Pagination active={active} getEducation={getEducation} pages={pages} />
        </div>
    )
}
