import React, { useState, useEffect } from 'react'
import Css from "./main.module.css"

export default function Main() {
    const [t, setT] = useState({})
    const UpdateTime = () => {
        let date = new Date;
        let d1 = new Date(); //"now"
        let d2 = new Date("2021/07/07");  // some date
        let diff = Math.abs(d1-d2);
        let day = Math.floor(diff / (24*60*60*1000))
        let time = {
            day: day,
            hours: date.getHours(),
            minutes: date.getMinutes(),
            seconds: date.getSeconds(),
        }
        setT(time)
    }
    useEffect(() => {
        UpdateTime()
        setInterval(() => UpdateTime(), 1000)
    }, [])




    return (
        <div className={Css.container}>
            <h1 className={Css.question1}>“Убаданы берген эмес, аны аткарган улук”</h1>
            <div className={Css.container_time}>
                <div className={Css.box_time}>
                    <p>{t?.day}</p>
                    <p>күн</p>
                </div>
                <div className={Css.box_time}>
                    <p>{t?.hours}</p>
                    <p>саат</p>
                </div>
                <div className={Css.box_time}>
                    <p>{t?.minutes}</p>
                    <p>мүнөт</p>
                </div>
                <div className={Css.box_time}>
                    <p>{t?.seconds}</p>
                    <p>секунд</p>
                </div>
            </div>
            <p className={Css.question2}>БАТКЕНГЕ БЕРИЛГЕН<br/>УБАДА АТКАРЫЛАБЫ?</p>
        </div >
    )
}
