import React from "react";
import Css from "./politicsCard.module.css";
import Card from "../../../assets/img/politics_card.png";
import {Link} from "react-router-dom";

export default function PoliticsCard({data}) {
  const date = new Date(data.created_at);
  return (
    <div className={Css.container}>
      <img src={Card} />
      <p className={Css.time}>{date.yyyymmdd()}</p>
      <p className={Css.text}>
        <p>{data.head}</p>
      </p>
      <Link to={`/politics/${data.id}`} className={Css.link}>
        <span>кененирээк </span>
        {">"}
      </Link>
    </div>
  );
}
