import React, { useState } from "react";
import Css from "./header.module.css";
import Logo from "../../assets/img/logo.svg";
import BurgerMenu from "../../assets/img/hamburger_menu.png";
import BurgerMenuBlue from "../../assets/img/hamburger_menu_blue.png";
import MenuBurger from "../menu_burger/menuBurger";
import { useLocation, Link } from "react-router-dom";
import Loop from "../../assets/img/magnifier.png"

export default function Header() {
  let location = useLocation()
  const [open, setOpen] = useState(false);
  return (
    <div>
      <header className={`${location.pathname == "/" ? "" : Css.header}`}>
        <div className={Css.box}>
          <div>
            <Link to="/"><img src={Logo} alt="Logo" className={Css.logomobile}/></Link>
            <ul className={Css.list}>
              <li>EN</li>
              <li>RU</li>
              <li>KG</li>
            </ul>
          </div>
          <div>
          <img src={Loop} className={Css.loop}/>
          <input type="text" className={Css.search}/>
          </div>
        </div>
        <MenuBurger open={open} setOpen={setOpen}/>
        <Link to="/"><img src={Logo} alt="Logo" className={Css.logo}/></Link>
        {location.pathname == "/" ? <img src={BurgerMenu} alt="BurgerMenu" onClick={() => setOpen(!open)} className={Css.burger}/> : <img src={BurgerMenuBlue} alt="BurgerMenu" onClick={() => setOpen(!open)} className={Css.burger}/>}
        <img src={BurgerMenu} alt="BurgerMenu" onClick={() => setOpen(!open)} className={Css.burgermobile}/>
      </header>
    </div>
  );
}

