import Css from "./lastNewsCard.module.css";
import TextBg from "../../../assets/img/last_news_textbg.png";
import CardBg from "../../../assets/img/last_news_cardbg.png";
import {Link} from "react-router-dom";

export default function LastNewsCard({data}) {
  return (
    <Link to={`/news/${data.id}`} className={Css.container} style={{backgroundImage: `url(${CardBg})`}}>
      <div className={Css.box_text}>
        <p dangerouslySetInnerHTML={{__html: data.head}}></p>
      </div>
    </Link>
  );
}
