import React, {useEffect, useState} from "react";
import Css from "./facts.module.css";
import Img from "../../assets/img/facts.png";
import {useParams} from "react-router";

export default function Facts({duration}) {
  const [data, setData] = useState([]);
  const { id } = useParams()
  const getEducation = () => {
    fetch(`http://176.126.164.176:8000/api/${duration ? duration : 'education'}/${id}`)
        .then((res) => res.json())
        .then((data) => {
          setData(data)
          console.log(data)
        });
  }
  useEffect(() => {
    getEducation()
  }, [])
  return (
    <div className={`${Css.container}`}>
      <p className={Css.title}>
          {data?.head}
        {/*Жаныбарлар жөнүндө 100*/}
        {/*<br />*/}
        {/*кызыктуу фактылар*/}
      </p>
        <br/>
        <br/>
      {/*<img src={Img} />*/}
      <div dangerouslySetInnerHTML={{__html: data?.description}}></div>
    </div>
  );
}
