import React from "react";
import Css from "./educationCard.module.css";
import Education from "../../../assets/img/education.png";
import {Link} from "react-router-dom";



export default function EducationCard({data}){
  const date = new Date(data.created_at);
  return (
    <div className={Css.container}>
      <img src={data.img ? data.img : Education} alt="card" />
      <div className={Css.box_text}>
        <p className={Css.title}>
          {data.head}
        </p>
        <p className={Css.time_link}>Мөөнөтү: {date.yyyymmdd()}</p>
        <p className={`${Css.text} ${Css.margin_tb}`} dangerouslySetInnerHTML={{__html: data.description}}>
        </p>
        <div className={Css.box}>
            <div/>
            <Link to={`/education/${data.id}`} className={Css.time_link}>Баардыгын окуу {">"}</Link>
        </div>
      </div>
    </div>
  );
}
